
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import nullData from "@/components/nullData.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import { mapGetters, mapActions, mapMutations } from "vuex";
import BScroll from 'better-scroll';
import tabBar from "@/components/tabBar.vue";
export default {
name: "subStoreList",
props: {},
components: {
loading,
XHeader,
tabBar,
nullData
},
data(){
return {
storeList: null,
goodsData: null, // 子店铺库存列表
plat: 3,
page: 1,
keyword: "",
endMore: false,
currentTab: 0,
isLoading: true
}
},
created(){
this.getBarList();
},
methods: {
async getBarList(){
let that = this;
let data = {
plat: this.plat,
page: this.page,
token: this.token,
account: this.account,
kw: this.keyword
}
const [err, res] = await api.sublist(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == "2000"){
if(res.data.list.length > 0){
that.storeList = res.data.list;
that.sub_member_id = res.data.list[0].sub_member_id;
}
that.getgoodslist()
}
},
async getgoodslist(){
let that = this;
this.isLoading = true;
let data = {
plat: this.plat,
account: this.account,
token: this.token,
page: this.page,
kw: this.keyword,
sub_member_id: this.sub_member_id?this.sub_member_id:''
}
const [err, res] = await api.goodslist(data);
if (err) {
this.$vux.toast.text(err.msg);
this.isLoading = false
return;
}
if(res.code == "2000"){

if(!that.goodsData){
that.goodsData = res.data.list;
that.page = that.page + 1;
}else{
if(res.data.list == ''){
return this.endMore = true;
}
this.goodsData = this.goodsData.concat(res.data.list);
this.page = this.page + 1;
}
}
this.isLoading = false
},
onTabClick(index){
if (index == this.currentTab) return;
this.currentTab = index;
this.page = 1;
this.goodsData = null;
this.endMore = false;
this.sub_member_id = this.storeList[index].sub_member_id;

this.getgoodslist();
},
checkCor(){
if (this.data.currentTab > 1) {
this.scrollLeft =  this.nav_item_width * (this.currentTab - 1)
} else {
this.scrollLeft =  0
}
},
replenishment(goods_commonid){
console.log(goods_commonid)
console.log(this.sub_member_id)
this.$router.push({
path: '/centerFull/myshop/replenishInfo',
query: {
goods_commonid,
sub_member_id: this.sub_member_id
}
})
},
handlClick(item){
this.onTabClick(item._index)
}
},
computed: {
...mapGetters(["user", "account", "token"])
},
}



